/* RESETS */
*,
*::before,
*::after {
    box-sizing: border-box;
}

    *:focus {
        outline: 3px dashed #228bec;
        outline-offset: 0;
    }

html {
    font: 62.5% / 1.15 sans-serif;
}

h1,
h2 {
    margin-bottom: 0;
}

ul {
    list-style: none;
    padding: 0;
}

button {
    border: none;
    margin: 0;
    padding: 0;
    width: auto;
    overflow: visible;
    background: transparent;
    color: inherit;
    font: inherit;
    line-height: normal;
    -webkit-font-smoothing: inherit;
    -moz-osx-font-smoothing: inherit;
    -webkit-appearance: none;
}

    button::-moz-focus-inner {
        border: 0;
    }

button,
input,
optgroup,
select,
textarea {
    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
}

button,
input {
    overflow: visible;
}

    input[type="text"] {
        border-radius: 0;
    }

body {
    width: 100%;
    max-width: 68rem;
    margin: 0 auto;
    font: 1.6rem/1.25 Arial, sans-serif;
    background-color: #f5f5f5;
    color: #4d4d4d;
}

@media screen and (min-width: 620px) {
    body {
        font-size: 1.9rem;
        line-height: 1.31579;
    }
}
/*END RESETS*/
/* GLOBAL STYLES */
.form-group > input[type="text"] {
    display: inline-block;
    margin-top: 0.4rem;
}

.btn {
    padding: 0.8rem 1rem 0.7rem;
    border: 0.2rem solid #4d4d4d;
    cursor: pointer;
    text-transform: capitalize;
}

    .btn.toggle-btn {
        border-width: 1px;
        border-color: #d3d3d3;
    }

        .btn.toggle-btn[aria-pressed="true"] {
            text-decoration: underline;
            border-color: #4d4d4d;
        }

.btn__danger {
    color: #fff;
    background-color: #ca3c3c;
    border-color: #bd2130;
}

.btn__filter {
    border-color: lightgrey;
}

.btn__primary {
    color: #fff;
    background-color: #000;
}

.btn-group {
    display: flex;
    justify-content: space-between;
}

    .btn-group > * {
        flex: 1 1 49%;
    }

        .btn-group > * + * {
            margin-left: 0.8rem;
        }

.label-wrapper {
    margin: 0;
    flex: 0 0 100%;
    text-align: center;
}

.visually-hidden {
    position: absolute !important;
    height: 1px;
    width: 1px;
    overflow: hidden;
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px);
    white-space: nowrap;
}

[class*="stack"] > * {
    margin-top: 0;
    margin-bottom: 0;
}

.stack-small > * + * {
    margin-top: 1.25rem;
}

.stack-large > * + * {
    margin-top: 2.5rem;
}

@media screen and (min-width: 550px) {
    .stack-small > * + * {
        margin-top: 1.4rem;
    }

    .stack-large > * + * {
        margin-top: 2.8rem;
    }
}

.stack-exception {
    margin-top: 1.2rem;
}
/* END GLOBAL STYLES */
.todoapp {
    background: #fff;
    margin: 2rem 0 4rem 0;
    padding: 1rem;
    position: relative;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 2.5rem 5rem 0 rgba(0, 0, 0, 0.1);
}

@media screen and (min-width: 550px) {
    .todoapp {
        padding: 4rem;
    }
}

.todoapp > * {
    max-width: 50rem;
    margin-left: auto;
    margin-right: auto;
}

.todoapp > form {
    max-width: 100%;
}

.todoapp > h1 {
    display: block;
    max-width: 100%;
    text-align: center;
    margin: 0;
    margin-bottom: 1rem;
}

.label__lg {
    line-height: 1.01567;
    font-weight: 300;
    padding: 0.8rem;
    margin-bottom: 1rem;
    text-align: center;
}

.input__lg {
    padding: 2rem;
    border: 2px solid #000;
}

    .input__lg:focus {
        border-color: #4d4d4d;
        box-shadow: inset 0 0 0 2px;
    }

[class*="__lg"] {
    display: inline-block;
    width: 100%;
    font-size: 1.9rem;
}

    [class*="__lg"]:not(:last-child) {
        margin-bottom: 1rem;
    }

@media screen and (min-width: 620px) {
    [class*="__lg"] {
        font-size: 2.4rem;
    }
}

.filters {
    width: 100%;
    margin: unset auto;
}
/* Todo item styles */
.todo {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

    .todo > * {
        flex: 0 0 100%;
    }

.todo-text {
    width: 100%;
    min-height: 4.4rem;
    padding: 0.4rem 0.8rem;
    border: 2px solid #565656;
}

    .todo-text:focus {
        box-shadow: inset 0 0 0 2px;
    }
/* CHECKBOX STYLES */
.c-cb {
    box-sizing: border-box;
    font-family: Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 1.25;
    display: block;
    position: relative;
    min-height: 44px;
    padding-left: 40px;
    clear: left;
}

    .c-cb > label::before,
    .c-cb > input[type="checkbox"] {
        box-sizing: border-box;
        top: -2px;
        left: -2px;
        width: 44px;
        height: 44px;
    }

    .c-cb > input[type="checkbox"] {
        -webkit-font-smoothing: antialiased;
        cursor: pointer;
        position: absolute;
        z-index: 1;
        margin: 0;
        opacity: 0;
    }

    .c-cb > label {
        font-size: inherit;
        font-family: inherit;
        line-height: inherit;
        display: inline-block;
        margin-bottom: 0;
        padding: 8px 15px 5px;
        cursor: pointer;
        touch-action: manipulation;
    }

        .c-cb > label::before {
            content: "";
            position: absolute;
            border: 2px solid currentColor;
            background: transparent;
        }

    .c-cb > input[type="checkbox"]:focus + label::before {
        border-width: 4px;
        outline: 3px dashed #228bec;
    }

    .c-cb > label::after {
        box-sizing: content-box;
        content: "";
        position: absolute;
        top: 11px;
        left: 9px;
        width: 18px;
        height: 7px;
        transform: rotate(-45deg);
        border: solid;
        border-width: 0 0 5px 5px;
        border-top-color: transparent;
        opacity: 0;
        background: transparent;
    }

    .c-cb > input[type="checkbox"]:checked + label::after {
        opacity: 1;
    }
body {
    background: #161616;
    color: #888;
    font-family: 'Ubuntu Mono', sans-serif;
    text-transform: uppercase;
}

.box {
    width: 90%;
    max-width: 450px;
    height: auto;
    margin: 0 auto;
    margin-top: 100px;
    background: #222;
    border-radius: 15px;
    box-shadow: 0px 3px 10px 1px rgba(0, 0, 0, 0.9);
}

.content {
    width: 85%;
    height: auto;
    margin: 0 auto;
    padding: 10px 0px;
    text-align: center;
}

h1 {
    font-size: 24px;
    font-weight: 400;
    color: #FFBF00;
    letter-spacing: 0.125em;
    text-align: center;
}

.field {
    width: 100%;
    margin: 10px auto;
    padding: 10px;
    background: #2c2c2c;
    border: none;
    box-shadow: 0px 1px 0px 0px #111;
    border-radius: 3px;
    outline: none;
    color: #FFBF00;
    font-weight: 700;
    letter-spacing: 0.125em;
    text-align: center;
    text-transform: uppercase;
}

::-webkit-input-placeholder { /* WebKit browsers */
    color: #5A5A5A;
}

:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color: #5A5A5A;
}

input[type="button"], input[type="submit"], button {
    margin: 10px auto;
    padding: 10px;
    background: #161616;
    border: none;
    box-shadow: 0px 1px 0px 0px #111;
    border-radius: 3px;
    outline: none;
    color: #FFBF00;
    font-weight: 700;
    letter-spacing: 0.125em;
    text-align: center;
    text-transform: uppercase;
}

input:hover {
    background: #FFBF00;
    color: #333;
}

input:active {
    background: #FACC2E;
    color: #333;
}

.btns {
    margin: 10px auto;
    padding: 10px;
    background: #161616;
    border: none;
    box-shadow: 0px 1px 0px 0px #111;
    border-radius: 30px;
    outline: none;
    color: #FFBF00;
    font-weight: 700;
    letter-spacing: 0.125em;
    text-align: center;
    text-transform: uppercase;
}

    .btns:hover {
        background: #FFBF00;
        color: #333;
    }

    .btns:active {
        background: #FACC2E;
        color: #333;
    }

table {
    text-align: left;
}

#menuTabs {
    margin-left: 60px;
    margin-right: 60px;
    text-align: center;
}

input[type="radio"] {
    cursor: pointer;
}

a {
    text-align: center;
    color: #FFBF00;
    cursor: pointer;
    text-decoration: none;
    text-decoration-line: none;
    text-decoration-style: initial;
    text-decoration-color: initial;
}

hr {
    background-color: #FFBF00;
    height: 3px;
}

